* {
  box-sizing: border-box; }

html {
  font-size: 62.5%;
  /* 16px * 62.5 = 10px = 1rem */ }

body {
  margin: 0;
  height: 100vh;
  font: 1.6rem Helvetica, Arial; }

.grid-container {
  display: grid;
  grid-template-areas: 'header' 'main' 'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%; }

.header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background-color: #203040;
  color: #fff; }
  .header a {
    color: #fff;
    font-weight: bold;
    text-decoration: none; }
    .header a:hover {
      color: #f08040; }
  .header .brand a {
    font-size: 3rem; }

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #203040;
  color: #fff; }
